import blog from '@/api/blog'
export default {
  	data () {
    	return {
    		title:'',
    		description:'',
    		content:'',
      		atIndex: false
    	}
  	},
  	methods:{
  		onCreate(){
  			blog.createBlog({ title: this.title, content: this.content, description: this.description, atIndex: this.atIndex })
  				.then(res => {
  					// 直接跳转
  					this.$message.success(res.msg);
  					this.$router.push({ path: `/detail/${res.data.id}`})

  					//	弹窗关闭之后跳转
					// let thisIn = this;
					// this.$message({
			        //   	message: res.msg,
			        //   	type: 'success',
			        //   	onClose:function(){
			        //   		thisIn.$router.push({ path: `/detail/${res.data.id}`})
			        //   	}
			        // });  	  									
  				})
  		},
  		// inputFunc(){
  		// 	this.title = this.title.length > 10 ? this.title.substr(0,10):this.title;
  		// 	console.log( this.title );
  		// }
  	}
}


